import React from "react";
import "./Procesos.css";
import Service from '../http-service';

import { Tree } from "rsuite";

class Procesos extends React.Component {
  constructor() {
    super();
    this.state = {
      data: [],
    };
  }
  componentDidMount(){
    this.get_data();
  }

  render() {
    return (
      <div className="table-v">
        <h1>Procesos</h1>
        <Tree data={this.state.data} defaultExpandAll onSelect={this.onSelect}/>
      </div>
    );
  }
  get_data = ()=>{
    Service.cpu().then(value => {
      let processes = this.process_to_tree(value);
      this.setState({data: processes })
    });
  }

  process_to_tree=(pr)=>{
    let tree = [];
    if(pr){
      pr.forEach((item,i,_) => {
        let tree_item = {
          label: item.Nombre,
          value: item.PID,
          children: this.process_to_tree(item.sub),
          item: item
        };
        tree.push(tree_item);
      });
      return tree;
    }
    return null;
  };

  onSelect=(val)=>{
    console.log(val);
  }

}

export default Procesos;
